.tool {
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.tool:before {
  content: url("icon-arrow-up-black.deb1d797.svg");
  width: 70px;
  height: 70px;
  margin-left: -2.5em;
  transition: all .65s cubic-bezier(.84, -.18, .31, 1.26), opacity .65s .5s;
  top: 100%;
  transform: scale(.6)translateY(-90%);
}

.tool:after {
  color: #edeff0;
  content: attr(data-tip);
  width: 600px;
  min-height: 172px;
  letter-spacing: 0;
  text-align: left;
  background: #000;
  margin-left: -8.75em;
  padding: 25px;
  font-size: 34px;
  font-weight: 300;
  line-height: 40px;
  transition: all .65s cubic-bezier(.84, -.18, .31, 1.26) .2s;
  top: 100px;
  left: 50%;
  transform: scale(.6)translateY(50%);
}

@media (max-width: 992px) {
  .tool:after {
    margin-left: -7.75em;
    padding: 20px 15px;
    font-size: 20px;
    line-height: 24px;
  }
}

@media (max-width: 1200px) {
  .tool:after {
    margin-left: -4.75em;
    padding: 20px;
    font-size: 22px;
    font-weight: 300;
    line-height: 28px;
  }
}

.tool:before, .tool:after {
  opacity: 0;
  z-index: -100;
  position: absolute;
  left: 50%;
}

.tool:hover:before, .tool:hover:after, .tool:focus:before, .tool:focus:after {
  opacity: 1;
  z-index: 100;
  transform: scale(1)translateY(0);
}

.tool:hover:before, .tool:focus:before {
  transition: all .65s cubic-bezier(.84, -.18, .31, 1.26) .2s;
}

.tool:hover:after, .tool:focus:after {
