//all common styling will add here


.container {
    @media (max-width: 1200px) {
      max-width: 1440px;
    }
    @media (max-width: $small-tablet) {
      max-width: 100%;
      padding: 0;
    }
  }
.loader-section {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $yellow;
  z-index: 2;
  position: relative;
  .loader-image {
    width: 110px;
    height: 110px;
    img {
      width: 100%;
    }
  }
}
.logoContainer{
  width: 55px;
  height: 55px;
}
.btn-blue {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  background-color: $bule;
  color: $white !important;
  font-family: 'atten_newmedium', sans-serif;
  letter-spacing: 0.1px;
  line-height: 23px;
  border: none;
  overflow: hidden;
  position: relative;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out;
  z-index: 1;
  span{
    z-index: 2;
    position: relative;
  }
  &:hover, &:active,  &:focus{
    background-color: $bule;
    color: $white;
  }
  &.slide-white{
    &:before {
      content: "";
      position: absolute;
      background: $white;
      bottom: 0;
      left: 0;
      right: 0;
      top: 100%;
      z-index: -1;
      transition: top 0.18s ease-out;
      width: 100%;
    }
    &:hover, &:active,  &:focus{
      &:before {
        top: 0;
        background-color: $white;
      }
      color: $dark-grey !important;
    }
  }
  &.slide-yellow{
    &:before {
      content: "";
      position: absolute;
      background: $yellow;
      bottom: 0;
      left: 0;
      right: 0;
      top: 100%;
      z-index: -1;
      transition: top 0.18s ease-out;
      width: 100%;
    }
    &:hover, &:active,  &:focus{
      &:before {
        top: 0;
        background-color: $yellow;
        z-index: 1;
      }
      color: $black !important;
    }
  }
  &.slide-black{
    &:before {
      content: "";
      position: absolute;
      background: $black;
      bottom: 0;
      left: 0;
      right: 0;
      top: 100%;
      z-index: -1;
      transition: top 0.18s ease-out;
      width: 100%;
    }
    &:hover, &:active,  &:focus{
      &:before {
        top: 0;
        background-color: $black;
        z-index: 1;
      }
      color: $white !important;
    }
  }
}
.map-wrapper{
  .btn-blue{
    &:hover, &:active,  &:focus{
      background-color: $bule-50;
    }
  }
}
.btn-h68{
  font-size: 24px;
  height: 68px;
  padding: 0 24px;
}
.btn-h57{
  font-size: 22px;
  height: 57px;
  padding: 0 24px;
}
.btn-h51{
  font-size: 19px;
  height: 57px;
  padding: 0 28px;
}
.btn-h30{
  font-size: 14px;
  height: 30px;
  padding: 0 16px;
}
.btn-w117{
  width: 117px;
}
.cust-input {
  width: 100%;
  border: 1px solid rgb(0 0 0 / 10%);
  height: 60px;
  padding: 10px;
  padding: 16px 22px;
  color: $black-80;
  font-family: 'atten_newregular', sans-serif;
  font-size: 19px;
  letter-spacing: 0;
  line-height: 29px;
  box-shadow: none;
  outline: none;
}
.overflow-hidden{
  overflow: hidden !important;
}
.mb-50{
  margin-bottom: 50px;
}
.mt-50{
  margin-top: 50px;
}
.mb-0{
  margin-bottom: 0;
}
.section-title{
  margin-bottom: 160px;
  text-align: center;
  @media (max-width: $small-tablet) {
    margin-bottom: 60px;
  }
  h2{
    color: $black;
    font-family: 'atten_newregular', sans-serif;
    font-size: 34px;
    letter-spacing: 0;
    line-height: 40px;
    text-align: center;
    margin-bottom: 28px;
  }
  img{
    transform: rotate(90deg);
    width: 28px;
  }
}
.main-banner-section {
  padding: 90px 0 190px 0;
  @media (max-width: $small-tablet) {
    padding: 90px 25px 90px;
  }
  .container{
    @media (max-width: $small-tablet) {
      padding: 0;
    }
  }
  .banner-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 45px;
    @media (max-width: $small-tablet) {
      flex-direction: column;
    }
    .banner-content {
      width: 50%;
      padding: 0 50px;
      @media (max-width: $small-tablet) {
        width: 100%;
        padding: 0;
      }
      .logo-wrapper {
        margin-bottom: 25px;
      }

      .text-wrapper {
        margin-bottom: 40px;
        max-width: 457px;
        @media (max-width: $small-tablet) {
          max-width: 100%;
        }
        h4 {
          color: $black;
          font-family: 'atten_newmedium', sans-serif;
          font-size: 35px;
          font-weight: 500;
          letter-spacing: 0;
          line-height: 41px;
          text-transform: uppercase;
        }
        p {
          color: $black-80;
          font-family: 'atten_newregular', sans-serif;
          font-size: 19px;
          letter-spacing: 0;
          line-height: 29px;
          margin-top: 37px;
          max-width: 388px;
          @media (max-width: $small-tablet) {
            max-width: 100%;
          }
        }
      }
    }

    .banner-slider {
      width: 50%;
      @media (max-width: $small-tablet) {
        width: 100%;
      }
      .slider-inner {
        .carousal-content {
          width: 100%;
          height: 450px;
          position: relative;
          transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
          background-color: $black;
          .carousal-image {
            width: 100%;
            height: 100%;
            opacity: 0.6;
            background-size: cover !important;
            background-repeat: no-repeat !important;
            background-position: center center !important;
            transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
            &.image-1 {
              background-image: url("../assets/images/banner-image1.jpg");
            }
            &.image-2 {
              background-image: url("../assets/images/banner-image2.jpg");
            }
            &.image-3 {
              background-image: url("../assets/images/banner-image3.jpg");
            }
          }
          .carousel-caption {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            padding: 0;
            margin: 0;
            right: auto;
            bottom: auto;
            z-index: 2;
            h5 {
              font-family: 'AmazObitaemOstrovV.2', sans-serif;
              font-size: 73px;
              letter-spacing: 0;
              line-height: 93px;
              @media (max-width: $small-tablet) {
                font-size: 33px;
                line-height: 53px;
              }
            }
          }
        }
      }
    }
  }
  .scroll-down {
    display: flex;
    justify-content: center;
    align-items: center;

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 5px;
      .logoScroll{
        width: 25px;
        height: 41px;
      }
      span {
        color: $white;
        font-size: 12px;
        font-weight: 500;
        letter-spacing: 0;
        line-height: 14px;
        text-align: center;
        text-transform: uppercase;
      }
    }

  }
  &.about-banner-section{
    padding: 90px 0 60px 0;
    .banner-wrapper {
      .banner-slider {
        .slider-inner {
          .carousal-image {
            &:before {
              background-color: $black-30;
            }
            &.image-1 {
              background-image: url("../assets/images/banner-image1.jpg");
            }
          }
        }
      }
    }
    .scroll-down {
      a {
        span {
          color: $lightGrey;
        }
      }
    }
  }
  &.knowledge-banner-section{
    padding: 90px 0 70px;
    @media (max-width: $small-tablet) {
      padding: 90px 25px 90px;
    }
  }
}
.nav-hover {
  .carousal-image {
    opacity: 0.25 !important;
    transition: background .3s, border-radius .3s, opacity .3s;
  }
}
.fade1,
.fade2,
.fade3,
.fade4,
.fade5,
.fade6,
.fade7,
.fade8,
.fade9,
.fade10,
.fade11,
.fade12,
.fade13{
  opacity: 1;
}
.showFade {
  animation: fadeInAnimation1 ease 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation1 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
